import React, { useEffect, useState } from "react";
import Grid from "./apps/Home/Grid";
import Nav from "./apps/Nav";
import "./index.css"

const App = () => {

  const API_KEY = "dc38013bb199457e8c4212bd93ea2057";

  let arr = ["US", "Election", "Bitcoin", "Coding", "Tech", "Sports", "Hollywood", "Music", "Artists"]

  const [stories, setStories] = useState([]);
  const [search, setSearch] = useState("");
  const [query, setQuery] = useState(
    arr[Math.floor(Math.random() * arr.length)]
  );
  const [loading, setloading] = useState(true);

  const getNews = async () => {
    const response = await fetch(
      `https://newsapi.org/v2/everything?q=${query}&pageSize=40&apiKey=${API_KEY}`
    );
    const data = await response.json();
    setStories(data.articles);
    setloading(false);
  };

  useEffect(() => {
    getNews();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [query]);

  const updateSearch = (e) => {
    setSearch(e.target.value);
  };

  const getSearch = (e) => {
    e.preventDefault();
    setQuery(search);
    setSearch("");
  };

  return (
    <div className="App">
      <Nav search={search} updateSearch={updateSearch} getSearch={getSearch} />
      <h2 style={{ textAlign: "center" }}>Search results for {query}</h2>
      {loading ? (
        <div className="loader">Loading.......</div>
      ) : (
          <Grid stories={stories} />
        )}
    </div>
  );
};

export default App;
