import React, { useState, useEffect } from "react";

export default function Card({ story }) {
  const [like, setLike] = useState(false);

  const ID = `${story.title}`;

  useEffect(() => {
    let localLike = "true" === localStorage.getItem(ID);
    setLike(localLike);
  }, [ID]);

  const toogleLike = () => {
    setLike((prev) => !prev);
    localStorage.setItem(ID, !like);
  };

  return (
    <div className="card">
      <div className="card-img">
        <img
          src={story.urlToImage || "https://s1.reutersmedia.net/resources_v2/images/rcom-default.png?w=800"}
          alt="img"
        />
      </div>
      <div className="card-content">
        <h2 className="card-headline">{story.title}</h2>
        <div className="card-expand">
          <p>{story.description}</p>
        </div>
        <div className="card-meta">
          <button className="like" onClick={toogleLike}>
            {like ? "üíñ" : "ü§ç"}
          </button>
          <div className="author">{`${story.source.name}`.slice(0, 30)}</div>
          <a href={story.url} className="read">
            Read More ‚Üí
          </a>
        </div>
      </div>
    </div>
  );
}
